<!--  -->
<template>
  <div class="child_view">
    <singer-list :keyWord="keyWord" :artistData="artistData"></singer-list>
  </div>
</template>

<script>
import { HttpManager } from '../../api';
import SingerList from '../../components/SingerList.vue';
export default {
  name: "SearchSinger",
  data () {
    return {
      keyWord: this.$route.query.key,
      artistData: {},
    };
  },

  components: { SingerList },

  methods: {
    getArtistList (val) {
      var that = this;
      HttpManager.getSearchByKeyWord("Artist", {
        key: this.keyWord,
        pn: val,
        rn: 30
      })
        .then((res) => {
          that.artistData = Object.assign({}, that.artistData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              artistList: res.data.artistList,
              pageSize: 30,
              getData: that.getArtistList,
            })
          // console.log(res.code);
          // that.code = parseInt(res.code)
          // that.total = parseInt(res.data.total);
          // that.artistList = res.data.artistList;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },

  computed: {},
  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.keyWord = newVal;
        this.getArtistList(1);
      },
      immediate: true, //监听首次绑定值
    },

  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
.artist {
  text-align: center;
  .name {
    position: relative;
    height: 22px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    flex-wrap: nowrap;
    justify-content: center;
    padding: 0 5px;
    .text {
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      overflow: hidden;
    }
  }
  .num {
    margin-top: 5px;
    font-size: 13px;
    color: #999;
  }
}
.pic_out {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
  border-radius: 50%;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.flex_c {
  display: flex;
  align-items: center;
}
</style>
<style lang="scss">
.artist {
  .item {
    width: 13% !important;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 30px;
  }
}
</style>



